.cover {
  width: 100%;
  height:max-content;
  top:0;
  background-color:rgba(0,0,0,0);
  background-size: cover;
  position:relative;
}

.animated {
  -webkit-animation-duration: 0.6s;
  animation-duration: 0.6s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes bounceInDown {
  0% {
    opacity: 0;
    transform: translateY(-2000px);
  }
  60% {
    opacity: 1;
    transform: translateY(30px);
  }
  80% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes bounceInDown {
  0% {
    opacity: 0;
    transform: translateY(-2000px);
  }
  60% {
    opacity: 1;
    transform: translateY(30px);
  }
  80% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0);
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

.bounceInDown {
  -webkit-animation-name: bounceInDown;
  animation-name: bounceInDown;
}

.center {
  margin: 0;
  color: white;
  width: 60%;
  text-align-last: justify;
  text-align:justify;
  text-justify:distribute-all-lines;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.name {
  font-size: 30px;
  font-weight: 900;
}

.greetings {
  font-size: 10px;
  font-weight: bold !important;
}

.overlay {
  background:rgba(0,0,0,0);
  position: absolute;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-bottom-style: solid;
  border-bottom-width: thin;
  top: 0;
  left: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
}

.arrow {
  position: absolute;
  bottom: 22%;
  display: block;
  left: 45%;
  text-align: center;
  align-items: center;
  width:max-content;
  z-index: 1;
  transition: all 0.5s ease;
  animation-delay: 0.5s;
  -webkit-animation-delay: 0.5s;
}

.arrow .btn {
  font-size: 20px;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}

.arrow:hover .btn {
  transform: scale(1.1);
}

.btn:focus {
  box-shadow: none !important;
}

@media only screen and (max-width: 960px) {
  .projects .subheader {
    padding-left: 30px;
    padding-right: 30px;
  }
  .arrow {
    position: absolute;
    bottom: 20%;
    display: block;
    left: 43%;
    text-align: center;
    align-items: center;
    width:max-content;
    z-index: 1;
    transition: all 0.5s ease;
    animation-delay: 0.5s;
    -webkit-animation-delay: 0.5s;
  }
  .arrow .btn {
    font-size: 18px;
    padding-top: 4px;
    padding-bottom: 4px;
    width: 100%;
  }
  
  .arrow:hover .btn {
    transform: scale(1.1);
  }
  
  .btn:focus {
    box-shadow: none !important;
  }
}


@media only screen and (max-width: 600px) {

  .projects .subheader {
    padding-left: 30px;
    padding-right: 30px;
  }

  .arrow {
    position: absolute;
    bottom: 20%;
    display: block;
    left: 39%;
    text-align: center;
    align-items: center;
    width:max-content;
    z-index: 1;
    transition: all 0.5s ease;
    animation-delay: 0.5s;
    -webkit-animation-delay: 0.5s;
  }
  .arrow .btn {
    font-size: 16px;
    padding-top: 2px;
    padding-bottom: 2px;
    width: 100%;
  }
  
  .arrow:hover .btn {
    transform: scale(1.1);
  }
  
  .btn:focus {
    box-shadow: none !important;
  }
}
