}

.overlay {
  background:rgba(0,0,0,0);
  position: absolute;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-bottom-style: solid;
  border-bottom-width: thin;
  top: 0;
  left: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
}

.arrow {
  position: absolute;
  bottom: 22%;
  display: block;
  left: 45%;
  text-align: center;
  align-items: center;
  width:-webkit-max-content;
  width:max-content;
  z-index: 1;
  transition: all 0.5s ease;
  animation-delay: 0.5s;
  -webkit-animation-delay: 0.5s;
}

.arrow .btn {
  font-size: 20px;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
}

.arrow:hover .btn {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.btn:focus {
  box-shadow: none !important;
}

